<template>
    <div style="height: 100%;">
    <el-table
      v-loading="loading"
      element-loading-text="Loading..."
      element-loading-spinner="el-icon-loading"
      :data="rankData.slice(this.page * this.pageSize - this.pageSize, this.page * this.pageSize)"
      style="width: 100%;min-height: 100%; max-height:100%; overflow: auto;">
      <el-table-column
        prop="index" label="Rank">
      </el-table-column>
      <el-table-column
      prop="tag"
      label="name">
      <template slot-scope="scope">
        <el-tag
          type='primary'
          disable-transitions>{{scope.row.name}}</el-tag>
      </template>
    </el-table-column>
    <el-table-column
      prop="tag"
      label="OpenRank">
      <template slot-scope="scope">
        <el-tag
          type='success'
          disable-transitions>{{scope.row.rank}}</el-tag>
      </template>
    </el-table-column>
    </el-table>
    <el-pagination style="margin-top: 3%;"
  background
  @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :page-size="this.pageSize"
      :pager-count="2"
      :page-sizes="[this.pageSize]"
      :total="this.rankData.length"
      layout="total, prev, pager, next">
</el-pagination>
    </div>
  </template>
  
  <script>
    export default {
      name: "RankComponent",
      props:{
        open: {
          type: Function,
          default: null
        },
        update: {
          type: Function,
          default: null
        },
        data: {
          type: Array,
          default: function() {
            return {
            }
          }
        }
      },
        watch: {
            data(newData) {
              // console.log(newData)
              this.page = 1
              // newData["index"] = this.tableData.length + 1
              this.rankData = newData
              this.loading = false
            },

            },
      methods: {
        deleteRow(index, rows) {
          rows.splice(index, 1);
        },
        handleCurrentChange(val) {
            this.page = val
        },
        handleSizeChange(val) {
          this.pageSize = val
        },
        OpenLoading() {
          this.loading = true
        },
        openChild(event, label) {
            this.$http.post('/data/search/', {label: label, blockNumberRange:[0, -1]}, {headers: {'Content-Type': 'application/json'}}, {emulateJSON: true}).then(function(res){
              this.open(res.body.json, true)
            })
          // }
        }
      },
      mounted(){
        // this.tableData = this.update()
        // this.timer = setInterval(()=>{
        //   this.tableData = this.update()
        //   console.log("更新Aberration...")
        // }, 1000)
      },
      destroyed() {
        if (this.timer != undefined) clearInterval(this.timer)
      },
      data() {
        return {
          page: 1,
          timer: undefined,
          loading: false,
          pageSize: 7,
          rankData: [
                        // {
                        //     "index": 0,
                        //     "name": "X-lab",
                        //     "rank": 20
                        // },
                        // {
                        //     "index": 1,
                        //     "name": "test",
                        //     "rank": 10
                        // },
                        // {
                        //     "index": 2,
                        //     "name": "test",
                        //     "rank": 10
                        // },
                        // {
                        //     "index": 3,
                        //     "name": "test",
                        //     "rank": 10
                        // },
                        // {
                        //     "index": 3,
                        //     "name": "test",
                        //     "rank": 10
                        // },
                ]
        }
      }
    }
  </script>