                        <!-- <el-option label="区域一" value="shanghai"></el-option> -->
                        <!-- <el-option label="区域二" value="beijing"></el-option> -->
                        </el-select>
                    </el-form-item>
                    <el-form-item label="年份">
                        <el-select v-model="networkSearchYear" placeholder="year">
                            <el-option v-for="(name, index) in totalYear" :key="index" :label="name" :value="name"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="月份">
                        <el-select v-model="networkSearchMonth" placeholder="month">
                            <el-option v-for="(name, index) in totalMonth" :key="index" :label="name" :value="name"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="NetworkSearch">查询</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>
        <div class="ns-result-area">
            <div class="ns-graph-area">
                <el-tabs v-model="activeName" @tab-click="handleClick" type="card">
                <el-tab-pane label="关系图" name="first">
                <el-empty description="无结果" v-show="!is_search"></el-empty>
                <div v-show="is_search" id="graph"></div>
                </el-tab-pane>
                </el-tabs>
            </div>
            <div class="ns-data-area">
                <div class="ns-info-area">
                    <div style="width: 90%; margin: 0 auto; margin-top: 2%;">
                        <div style="font-size:larger;text-align:center;margin-bottom: 5%;">查询信息</div>
                        <el-descriptions column=2>
                            <template slot="extra">
                            </template> 
                                <el-descriptions-item label="仓库名"><el-tag size="small">{{ this.info.name }}</el-tag></el-descriptions-item>
                                <el-descriptions-item label="时间"><el-tag size="small">{{ this.info.year }}-{{ this.info.month }}</el-tag></el-descriptions-item>
                                <el-descriptions-item label="用户数"><el-tag size="small" type="primary">{{ this.info.userNumber }}</el-tag></el-descriptions-item>
                                <el-descriptions-item label="pr数">
                                <el-tag size="small" type="primary">{{ this.info.pr }}</el-tag>
                                </el-descriptions-item>
                                <el-descriptions-item label="issue数"><el-tag size="small" type="primary">{{ this.info.issue }}</el-tag></el-descriptions-item>
                        </el-descriptions>
                    </div>
                </div>
                <div class="ns-rank-area">
                    <RankComponent :data="openRank"></RankComponent>
                </div>
            </div>
        </div>
        <!-- <div class="search-area">
          <div class="upload-info">
            <el-descriptions title="上传信息">
              <template slot="extra">
              </template> 
                <el-descriptions-item label="账户数"><el-tag size="small">1</el-tag></el-descriptions-item>
                <el-descriptions-item label="交易数"><el-tag size="small">1</el-tag></el-descriptions-item>
                <el-descriptions-item label="区块范围"><el-tag size="small" type="primary">1</el-tag>~<el-tag type="primary" size="small"></el-tag></el-descriptions-item>
                <el-descriptions-item label="异常行为数">
                  <el-tag size="small" type="primary">1</el-tag>
                </el-descriptions-item>
                <el-descriptions-item label="时间戳"><el-tag size="small" type="primary">1</el-tag></el-descriptions-item>
            </el-descriptions>
          </div>
        </div> -->
      </div>
    </div>
</template>
