                    this.info = res.body.info
                    // this.DrawPie(res.body.pie)
                    this.DrawLine(res.body.line.issue, res.body.line.request, res.body.line.user)
                }, function (err) {
                    this.openRank = [{"index": 1, "name": "aaa", "rank": 20}]
                    this.is_search = true
                    this.DrawLine(this.issue, this.request, this.user)
                    // this.DrawPie(this.pie_data)
                }) 
