<template>
    <div style="width: 100%;">
      <div class="upload-content">
        <div class="user-search-area">
            <div class="search-form">
                <el-form :inline="true" :model="formInline" class="demo-form-inline">
                    <el-form-item label="用户名">
                        <el-input
                            placeholder="请输入内容"
                            v-model="name"
                            clearable>
                            </el-input>
                    </el-form-item>
                    <el-form-item label="">
                            <el-date-picker
                            v-model="date"
                            type="monthrange"
                            align="right"
                            unlink-panels
                            range-separator="~"
                            start-placeholder="开始月份"
                            end-placeholder="结束月份"
                            :picker-options="pickerOptions">
                            </el-date-picker>
                    </el-form-item>
                    <!-- <el-form-item label="月份">
                        <el-select v-model="formInline" placeholder="month">
                        <el-option label="区域一" value="shanghai"></el-option>
                        <el-option label="区域二" value="beijing"></el-option>
                        </el-select>
                    </el-form-item> -->
                    <el-form-item>
                        <el-button type="primary" @click="UserAnalyze">查询</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>
        <div class="user-result-area">
            <div class="user-graph-area">
                <el-tabs v-model="activeName" @tab-click="handleClick" type="card">
                <el-tab-pane label="关系图" name="first">
                    <el-empty description="无结果" v-show="!is_search"></el-empty>
                    <div v-show="is_search" id="user-graph"></div>
                </el-tab-pane>
                </el-tabs>
            </div>
            <div class="user-data-area">
                <div class="user-info-area">
                    <div style="width: 90%; margin: 0 auto; margin-top: 2%;">
                        <div style="font-size:larger;text-align:center;margin-bottom: 5%">查询信息</div>
                        <el-descriptions column=1>
                            <template slot="extra">
                            </template> 
                                <el-descriptions-item label="用户名"><el-tag size="small">{{ this.info.name }}</el-tag></el-descriptions-item>
                                <el-descriptions-item label="时间"><el-tag size="small">{{ this.info.time.start.year }}-{{ this.info.time.start.month }} ~ {{ this.info.time.end.year }}-{{ this.info.time.end.month }}</el-tag></el-descriptions-item>
                                <!-- <el-tag size="small" type="primary">1</el-tag> -->
                                <!-- </el-descriptions-item> -->
                        </el-descriptions>
                        <el-descriptions column=3>
                            <template slot="extra">
                            </template> 
                                <el-descriptions-item label="参与项目数"><el-tag size="small">{{ this.info.project }}</el-tag></el-descriptions-item>
                                <el-descriptions-item label="参与pr数"><el-tag size="small">{{ this.info.pr }}</el-tag></el-descriptions-item>
                                <!-- <el-tag size="small" type="primary">1</el-tag> -->
                                <!-- </el-descriptions-item> -->
                                <el-descriptions-item label="issue数"><el-tag size="small" type="primary">{{ this.info.issue }}</el-tag></el-descriptions-item>
                        </el-descriptions>
                    </div>
                </div>
                <div class="user-line-area">
                    <el-empty description="无结果" v-show="!is_search" :image-size="100"></el-empty>
                    <div v-show="is_search" id="user-line" style="width: 390px;height: 250px"></div>
                    <!-- <RankComponent :data="aberration" :open="popper"></RankComponent> -->
                </div>
                <div class="user-pie-area">
                    <el-empty description="无结果" v-show="!is_search" :image-size="100"></el-empty>
                    <div v-show="is_search" id="user-pie" style="width: 390px;height: 250px"></div>
                </div>
            </div>
        </div>
      </div>
    </div>
</template>
<script>
import RankComponent from "@/components/Rank.vue"
// import TableComponent from "@/components/Table.vue"
// import EdgeInfo from "./components/EdgeInfo.vue";
// import NetGraph from "@/components/NetGraph.vue";
export default ({
    name: "UserAnalyzeComponent",
    // props:{
    //     open: {
    //       type: Function,
    //       default: null
    //     },
    //     popper: {
    //       type: Function,
    //       default: null
    //     }
    //   },
    data() {
        return {
            is_search: false,
            activeName: 'first',
            name: '',
            pie_data: {
                "label": ['X-lab2017-open-wonderland', 'X-lab2017-open-research', 'X-lab2017-open-perf', 'X-lab2017-open-digger', 'X-lab2017-open-leaderboard'],
                "data": [{"name": "X-lab2017-open-wonderland", "value": 2}, {"name": "X-lab2017-open-research", "value": 1}, {"name": "X-lab2017-open-digger", "value": 1} ,{"name":"X-lab2017-open-perf", "value": 2}, {"name": "X-lab2017-open-leaderboard", "value" : 3}]
            },
            line_data: {
                "project": [["2023-7", 1], ["2023-8", 2], ["2023-9", 5], ["2023-10", 6], ["2023-11", 1], ["2023-12", 5]],
                "issue": [["2023-7", 2], ["2023-8", 3], ["2023-9", 4], ["2023-10", 6], ["2023-11", 1], ["2023-12", 5]],
                "pr": [["2023-7", 1], ["2023-8", 2], ["2023-9", 3], ["2023-10", 7], ["2023-11", 1], ["2023-12", 5]],
                "x": ["2023-7", "2023-8", "2023-9", "2023-10", "2023-11", "2023-12"]
            },
            date: undefined,
            graph: undefined,
            info: {
                "name": 'Unknown',
                "time": {
                    "start":{
                        "year": 'Unknown',
                        "month": 'Unknown'
                    },
                    "end": {
                        "year": 'Unknown',
                        "month": 'Unknown'
                    }
                },
                "project": 0,
                "pr": 0,
                "issue": 0 
            },
            empty: true,
                chart: undefined,
                graph: undefined,
                nodesNumber: 0,
                edgesNumber: 0,
            nodes: [
            {"id": "0x75cd2467f8dd731a5acb927bd79c4cb361210db6", "degree": 2, "cluster": -1},
            {"id":"0xc611952d81e4ecbd17c8f963123dec5d7bce1c27", "degree": 2},
            {"id":"0x4de23f3f0fb3318287378adbde030cf61714b2f3", "degree":1}
            ],
            edges:[
    {"source": "0x75cd2467f8dd731a5acb927bd79c4cb361210db6", "target": "0xc611952d81e4ecbd17c8f963123dec5d7bce1c27", "value": "299928147539103754", "tx_hash": "0xa1a658f372e0637d00b78753036068d71d48da9d6a924e8f6443bc3e5bbec940", "blockNumber": "15265907"},
    {"source": "0x75cd2467f8dd731a5acb927bd79c4cb361210db6", "target": "0x4de23f3f0fb3318287378adbde030cf61714b2f3", "value": "9000000000000000", "tx_hash": "0x0dcf54ffa7d83238b8a44a9c2385a2d58a9f0d326116f293ba351076dbb38d4a", "blockNumber": "15265929"},
    {"source": "0x8bc90377daf1b4e71686d025c88b2178089cf3e8", "target": "0xc611952d81e4ecbd17c8f963123dec5d7bce1c27", "value": "2611540377663885806", "tx_hash": "0x5ff169a9301adf698f6e78bd8c715aebb7fdd5404e24ef5b926a10adbc1b8772", "blockNumber": "15265903"},
    {"source": "0x1a26db1b2baf0b23f18e19375e8fdc159feb707e", "target": "0xc611952d81e4ecbd17c8f963123dec5d7bce1c27", "value": "473558773599046477", "tx_hash": "0xc708b1f3315db9966a677782efc896364803cb457cdfc2ae76ff9141935fd532", "blockNumber": "15265905"},
    {"source": "0x43da6d2db9651b7042e31ffb2607a7cfa4d5d03b", "target": "0xc611952d81e4ecbd17c8f963123dec5d7bce1c27", "value": "621618643271362538", "tx_hash": "0x51927ba1b63a5adf90dc8a673b459c9e5e3e42c2ae3157ca57ae7a54232c75a0", "blockNumber": "15265905"},
    {"source": "0xe5c04c954c5494f6975f63e3f19957a380648f82", "target": "0xc611952d81e4ecbd17c8f963123dec5d7bce1c27", "value": "223324337176025309", "tx_hash": "0x0c3ecd77775785129d70adcb43c656c854f22699f3350aa17693af608d3729ee", "blockNumber": "15265907"},
    {"source": "0x795d8f8b2bf1bb23e99e165c8e4fa067d96cb00a", "target": "0xc611952d81e4ecbd17c8f963123dec5d7bce1c27", "value": "216300000000000000", "tx_hash": "0xf59060ff06f14fd261a81272b562afe7c4a9a5642ead937962858a8f54a601b9", "blockNumber": "15265907"},
    {"source": "0xbdf4cf8269c3883dd88975e1978a6aa9d3877f2e", "target": "0xc611952d81e4ecbd17c8f963123dec5d7bce1c27", "value": "288173851825282417", "tx_hash": "0x851c13f6fd11fe61bc40e6f071d1bd1fb2b667adbed715534c3a631c48c5eb49", "blockNumber": "15265907"},
    {"source": "0x1e29e2cefd3395d892678add3eb791ed74114f3b", "target": "0xc611952d81e4ecbd17c8f963123dec5d7bce1c27", "value": "284693896987212129", "tx_hash": "0x1ddbf38bf57786d3e1079cc53a81168f0b7ac958b838bdfeb6a77dfb1cc134af", "blockNumber": "15265907"},
    {"source": "0x47537db3dfec13e9f20fb4f4cd0cf26e2cc37fdd", "target": "0xc611952d81e4ecbd17c8f963123dec5d7bce1c27", "value": "265102276730551319", "tx_hash": "0x24dca1ae019a880520049e5ac75d6a49c009cb3dfe59de0b6261859070ec0ae3", "blockNumber": "15265907"},
    {"source": "0x4842336fdaf0405e12c7e968dc1998856672a4d7", "target": "0xc611952d81e4ecbd17c8f963123dec5d7bce1c27", "value": "373844808070480320", "tx_hash": "0xec5da3e3ec57dc29b534888f9874b1733f0d425518180bce2ba8e2511ac4119c", "blockNumber": "15266086"},
    {"source": "0x6e8b6af9d8b402d89d1f5d8c1cf535850dc28b98", "target": "0xc611952d81e4ecbd17c8f963123dec5d7bce1c27", "value": "116709184816773670", "tx_hash": "0x8992fd93bc4dd5fb7a811718313802cc4b6043a9aad6ef847c35ebb47326273a", "blockNumber": "15266104"},
    {"source": "0xa789d472cfef01e09674e4a6c03b35c72e0bfdb0", "target": "0xc611952d81e4ecbd17c8f963123dec5d7bce1c27", "value": "1847142590831247533", "tx_hash": "0x18e0e10013a4d14535f465e54f051c34607f4c654b2c59a6ac58e5588ee07363", "blockNumber": "15266490"},
        ]
        }
    },
    components: {
        RankComponent
        // TableComponent,
        // NetGraph
    }, 
    mounted() {
        // this.drawUserGraph(this.nodes,this.edges)
        // this.drawUserLine()
        // this.drawUserPie()
    },
    methods: {
        UserAnalyze: function() {
            let startYear = this.date[0].getYear() + 1900
            let startMonth = this.date[0].getMonth() + 1
            let endYear = this.date[1].getYear() + 1900
            let endMonth = this.date[1].getMonth() + 1
            console.log("UserAnalyze", this.name, startYear, startMonth, endYear, endMonth)
            this.$http.post('/api/UserAnalyze/', {name: this.name, startYear: startYear, startMonth: startMonth, endYear: endYear, endMonth: endMonth}, {headers: {'Content-Type': 'application/json'}}, {emulateJSON: true}).then(function (res) {
                    // this.token = res.body.token
                    this.code = 200
                    this.is_search = true
                    this.info = res.body.info
                    this.drawUserPie(res.body.pie.data, res.body.pie.label)
                    this.drawUserGraph(res.body.option.nodes, res.body.option.edges)
                    this.drawUserLine(res.body.line.project, res.body.line.pr, res.body.line.issue, res.body.line.x)
                }, function (err) {
                    // this.DrawLine(this.issue, this.request, this.user)
                    this.is_search = true
                    this.drawUserPie(this.pie_data["data"], this.pie_data["label"])
                    // this.drawUserGraph(this.nodes, this.edges)
                    this.drawUserLine(this.line_data["project"], this.line_data["pr"], this.line_data["issue"], this.line_data["x"])
                }) 
        },
        drawUserGraph: function (data_nodes, data_links) {
                if (this.chart != undefined) {
                    this.chart.destroy()
                }
                if (this.graph != undefined) {
                  this.graph.destroy()
                  this.graph = undefined
                }
                // this.nodesNumber = nodes.length
                // this.edgesNumber = edges.length
                const tooltip = new this.$antv.Tooltip({
    offsetX: 10,
    offsetY: 10,
    // the types of items that allow the tooltip show up
    // 允许出现 tooltip 的 item 类型
    itemTypes: ['node', 'edge'],
    // custom the tooltip's content
    // 自定义 tooltip 内容
    getContent: (e) => {
      const outDiv = document.createElement('div');
      // outDiv.style.width = 'fit-content';
      outDiv.style.padding = '0px 40px 0px 20px';
      // node类型
      // {"id":"44269","n":"X-lab2017/open-digger","c":"r","i":25.29,"r":0.15,"v":24.81}
      function getCategory(c){
          switch (c){
            // 仓库
            case "r":
                return "repository"
            // 用户
            case "u":
                return "user"
            // issue
            case "i":
                return "issue"
            // pr
            default:
                return "pull request"
          }
      }
      if (e.item.getType() == 'node'){
        outDiv.innerHTML = `
        <h4>节点信息</h4>
        <ul>
            <li>ID: ${e.item.getModel().id}</li>
            <li>名称: ${e.item.getModel().n}</li>
            <li>类别: ${getCategory(e.item.getModel().c)}</li>
            <li>OpenRank值: ${e.item.getModel().v}</li>
        </ul>`;
      }
      // edge类型
      else if (e.item.getType() == 'edge'){
        outDiv.innerHTML = `
        <h4>边信息</h4>
        <ul>
          <li>起点ID: ${e.item.getModel().source}</li>
          <li>终点ID: ${e.item.getModel().target}</li>
          <li>边权重: ${e.item.getModel().weight}</li>
        </ul>
        `;
      }
      return outDiv;
    },
    shouldBegin: (e) => {
      // console.log(e.target);
      let res = true;
      switch (e.item.getModel().id) {
        case '1':
          res = false;
          break;
        case '2':
          if (e.target.get('name') === 'text-shape') res = true;
          else res = false;
          break;
        case '3':
          if (e.target.get('name') !== 'text-shape') res = true;
          else res = false;
          break;
        default:
          res = true;
          break;
      }
      return res;
    },
  });
  const container = document.getElementById('user-graph');
    const width = 800
    const height = 600
    // console.log(document.documentElement.scrollHeight * 0.92)
    // console.log(document.body.scrollHeight)
    const topK = 10;
    const default_legend_size = 20
    const default_legend_font_size = 15
    const typeConfigs = {
      'r': {
        type: 'rect',
        size: default_legend_size,
        style: {
          fill: '#5B6FBF'
        }
      },
      'u': {
        type: 'rect',
        size: default_legend_size,
        style: {
          fill: '#DE6F6A'
        }
      },
      'i': {
        type: 'rect',
        size: default_legend_size,
        style: {
          fill: '#9EC97F'
        }
      },
      'p': {
        type: 'rect',
        size: default_legend_size,
        style: {
          fill: '#F2C96C'
        }
      },
    }

    const legendData = {
      nodes: [
        { id: 'r', label: 'repo', ...typeConfigs['r'], labelCfg: { style: { fontSize: default_legend_font_size } } },
        { id: 'u', label: 'user', ...typeConfigs['u'], labelCfg: { style: { fontSize: default_legend_font_size } } },
        { id: 'i', label: 'issue', ...typeConfigs['i'], labelCfg: { style: { fontSize: default_legend_font_size } } },
        { id: 'p', label: 'pull', ...typeConfigs['p'], labelCfg: { style: { fontSize: default_legend_font_size } } }
      ]
    }

    const legend = new this.$antv.Legend({
      data: legendData,
      align: 'center',
      layout: 'horizontal', // vertical
      // position: 'bottom-left',
      vertiSep: 12,
      horiSep: 24,
      offsetY: height - 75,
      padding: [4, 16, 8, 16],
      containerStyle: {
        fill: '',
        // 无外边框
        lineWidth: 0
      },
      // 标题
      title: ' ',
      titleConfig: {
        position: 'center',
        offsetX: 0,
        offsetY: 0,
      },
      filter: {
        enable: true,
        multiple: true,
        trigger: 'click',
        graphActiveState: 'activeByLegend',
        graphInactiveState: 'inactiveByLegend',
        filterFunctions: {
          'r': (d) => {
            if (d.c === 'r') return true;
            return false
          },
          'u': (d) => {
            if (d.c === 'u') return true;
            return false
          },
          'i': (d) => {
            if (d.c === 'i') return true;
            return false
          },
          'p': (d) => {
            if (d.c === 'p') return true;
            return false
          },
        }
      }
    });

    const toolbar = new this.$antv.ToolBar({
      position: { x: 25, y: 10 },
    });

    const graph_ = new this.$antv.Graph({
      container: 'user-graph',
      width,
      height,
      layout: {
        preventOverlap: true,
        type: 'force',
        nodeSpacing: 15,
      },
      defaultNode: {
        size: 15,
        lineWidth: 0,
        style: {
          lineWidth: 0,
        }
      },
      nodeStateStyles: {
        activeByLegend: {
          lineWidth: 3,
          strokeOpacity: 0.5,
          stroke: '#f00'
        },
        inactiveByLegend: {
          opacity: 0.5,
          lineWidth: 0,
          strokeOpacity: 0,
          stroke: ''
        },
        dark: {
          lineWidth: 0,
          opacity: 0.2,
        },
        mouseenterState: {
          lineWidth: 3,
          strokeOpacity: 0.5,
          // stroke: '#f00'
          stroke: '#5B8FF9'
        },
        noLineWidthState: {
          lineWidth: 0
        }
      },
      edgeStateStyles: {
        mouseenterState: {
          lineWidth: 1.5,
          stroke: '#999',
        },
      },
      plugins: [legend, tooltip, toolbar],
      modes: {
        //default: ['drag-canvas', 'drag-node', 'zoom-canvas',{
        //  type: 'activate-relations',
        //activeState: 'activeByLegend',
        //inactiveState: 'inactiveByLegend',
        //},],
        default: ['drag-canvas', 'drag-node', 'zoom-canvas'],
      },
    });
    function getColor(node_type) {
      switch (node_type) {
        // 仓库
        case "r":
          return "#5B6FBF"
        // 用户
        case "u":
          return "#DE6F6A"
        // issue
        case "i":
          return "#9EC97F"
        // pr
        default:
          return "#F2C96C"
      }
    }

    function getSize(v, base, threshold) {
      if (v <= threshold) {
        return base * v
      } else {
        return getSize(v - threshold, base / 2, threshold) + threshold * base
      }
    }

    function getKthLargest(nodes) {
      nodes.sort((a, b) => b.v - a.v);
      return nodes[topK - 1].v;
    }

    function getLabel(node_type, name) {
      switch (node_type) {
        // 仓库
        case "r":
          return name
        // 用户
        case "u":
          return name
        // issue
        case "i":
          return "#" + name
        // pr
        default:
          return "#" + name
      }
    }

        let nodes = data_nodes
        let  edges = data_links.map(function (link, i) {
            link.source = link.s
            link.target = link.t
            link.weight = link.w
            return Object.assign({}, link);
          })
        nodes.forEach(node => {
          node.size = getSize(node.v, 6, 2)
          node.style = {
            fill: getColor(node.c),
            lineWidth: 0,
          };
          if (node.v >= getKthLargest(nodes)) {
            node.label = getLabel(node.c, node.n)
            node.labelCfg = {
              position: 'bottom',
            }
          }
        });



        graph_.data({
          nodes: nodes,
          edges: edges
        });


        graph_.render();

        graph_.on('node:dragstart', function (e) {
          graph_.layout();
          refreshDragedNodePosition(e);
        });
        graph_.on('node:drag', function (e) {
          const forceLayout = graph_.get('layoutController').layoutMethods[0];
          forceLayout.execute();
          refreshDragedNodePosition(e);
        });
        graph_.on('node:dragend', function (e) {
          e.item.get('model').fx = null;
          e.item.get('model').fy = null;
        });
        graph_.on('node:click', (evt) => {
          const { item } = evt;
          item.toFront();
          // graph.setItemState(item, 'selected', true);
        });

        function clearAllStats() {
          graph_.setAutoPaint(false);
          graph_.getNodes().forEach(function (node) {
            graph_.clearItemStates(node);
            graph_.setItemState(node, 'noLineWidthState', true);
          });
          graph_.getEdges().forEach(function (edge) {
            graph_.clearItemStates(edge);
            graph_.setItemState(edge, 'noLineWidthState', true);
          });
          graph_.paint();
          graph_.setAutoPaint(true);
        }

        // 鼠标移动到节点
        graph_.on('node:mouseenter', function (e) {
          const item = e.item;
          graph_.setAutoPaint(false);
          // 修改所有节点样式
          graph_.getNodes().forEach(function (node) {
            graph_.clearItemStates(node);
            graph_.setItemState(node, 'dark', true);
          });
          // 修改选中节点样式
          graph_.setItemState(item, 'dark', false);
          graph_.setItemState(item, 'mouseenterState', true);
          // 修改所有边的样式
          graph_.getEdges().forEach(function (edge) {
            if (edge.getSource() === item) {
              graph_.setItemState(edge.getTarget(), 'dark', false);
              graph_.setItemState(edge.getTarget(), 'mouseenterState', true);
              graph_.setItemState(edge, 'mouseenterState', true);
              edge.toFront();
            } else if (edge.getTarget() === item) {
              graph_.setItemState(edge.getSource(), 'dark', false);
              graph_.setItemState(edge.getSource(), 'mouseenterState', true);
              graph_.setItemState(edge, 'mouseenterState', true);
              edge.toFront();
            } else {
              graph_.setItemState(edge, 'mouseenterState', false);
            }
          });
          graph_.paint();
          graph_.setAutoPaint(true);
        });
        graph_.on('node:mouseleave', clearAllStats);
        graph_.on('canvas:click', clearAllStats);

        if (typeof window !== 'undefined')
          window.onresize = () => {
            if (!graph_ || graph_.get('destroyed')) return;
            if (!container || !container.scrollWidth || !container.scrollHeight) return;
            graph_.changeSize(container.scrollWidth, container.scrollHeight);
          };

    function refreshDragedNodePosition(e) {
      const model = e.item.get('model');
      model.fx = e.x;
      model.fy = e.y;
    }
        this.graph = graph_
